import React from 'react';
import './App.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUndo } from '@fortawesome/free-solid-svg-icons'
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';

class App extends React.Component {
  render() {
    return (
      <div className="game">
        <div className="game-board">
          <Board />
        </div>
      </div>
    );
  }
}


class Board extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      misses: 0,
      cups: Array(10).fill(0),
      hittedCups: [],
    };
  }

  renderCup(i) {
    return (<Cup value={this.state.cups[i]} onClick={() => this.handleClick(i)} />);
  }

  calcHitrate(cups, misses){
    const accumulation = (accumulator, currentValue) => accumulator + currentValue;
    if(cups.reduce(accumulation) + misses > 0){
      return Math.round((cups.reduce(accumulation)/(cups.reduce(accumulation) + misses))*100)
    }else{
      return 0;
    }
  }

  handleUndo(){
    const hittedCups = this.state.hittedCups.slice();
    if(hittedCups.length > 0){
      const lastHit = hittedCups.pop();
      if(lastHit === -1){
        this.setState({
          misses: this.state.misses - 1,
          hittedCups: hittedCups})
      }else{
        const cups = this.state.cups.slice();
        cups[lastHit] = cups[lastHit] - 1
        this.setState({
          cups: cups,
          hittedCups: hittedCups})
      }
    }
  }

  handleMiss(){
    const hittedCups = this.state.hittedCups.slice();
    hittedCups.push(-1);
    const misses = this.state.misses + 1;
    this.setState({misses: misses,
      hittedCups: hittedCups
    });
  }

  handleClick(i) {
    const hittedCups = this.state.hittedCups.slice();
    hittedCups.push(i);
    const cups = this.state.cups.slice();
    cups[i] =  this.state.cups[i]+1;
    this.setState({cups: cups,
      hittedCups: hittedCups
    });
  }

  render() {
    return (
      <Container>
        <Row className="justify-content-center">
          <Button variant="outline-primary btn-miss" size="lg"
          onClick={() => this.handleMiss()}
          >Misses: {this.state.misses}</Button>
          <Button variant="outline-primary btn-undo" size="lg"
          onClick={() => this.handleUndo()}
          ><FontAwesomeIcon icon={faUndo} /></Button>
        </Row>
        <Row className="justify-content-center">
          {this.renderCup(0)}
          {this.renderCup(1)}
          {this.renderCup(2)}
          {this.renderCup(3)}
        </Row>
        <Row className="justify-content-center">
          {this.renderCup(4)}
          {this.renderCup(5)}
          {this.renderCup(6)}
        </Row>
        <Row className="justify-content-center">
          {this.renderCup(7)}
          {this.renderCup(8)}
        </Row>
        <Row className="justify-content-center">
          {this.renderCup(9)}
        </Row>
        <Row className="justify-content-center">
          Hitrate: {this.calcHitrate(this.state.cups, this.state.misses)}%
        </Row>
        </Container>
    );
  }
}

class Cup extends React.Component {
  render() {
    return (
      <Button variant="outline-danger" size="lg" className="cup"
      onClick={() => this.props.onClick()}>
      {this.props.value}
      </Button>
    );
  }
}

export default App;
